    function CrudUserbaseListComponent(userbaseService, roleMappingService, alertService) {
        this.userbaseService = userbaseService;
        this.roleMappingService = roleMappingService;
        this.alertService = alertService;
    }
    CrudUserbaseListComponent.prototype.ngOnInit = function () {
        this.getAll_Records();
        this.getRoles();
    };
    // IMPORT DATA FROM SERVICE
    CrudUserbaseListComponent.prototype.getAll_Records = function (query) {
        var _this = this;
        this.userbaseService.getAllRecords(query).subscribe(function (data) {
            var data_mapped = data.map(function (x) {
                return {
                    id: x.id,
                    firstname: x.firstname,
                    lastname: x.lastname,
                    profile: x.profile,
                    realm: x.realm,
                    username: x.username,
                    password: x.password,
                    email: x.email,
                    emailVerified: x.emailVerified,
                    verificationToken: x.verificationToken,
                    memberId: x.memberId,
                    user_legal_id: x.user_legal_id,
                    user_internal_id: x.user_internal_id,
                    user_photo_path: x.user_photo_path,
                    // Optional
                    roles: x.roles,
                    roleMapping: x.roles,
                    profile_json: x.roles[0],
                    emailVerified_json: { id: 0, name: x.emailVerified }
                };
            });
            _this.crudRecord.rows = data_mapped;
            // this.crudRecord.currentRecord = data_mapped[0];
            _this.crudRecord.rows_original = data_mapped;
            // this.show_data = true;
            _this.crudRecord.alertMessage = new AlertModel();
        }, function (error) {
            // this.show_data = false;
            _this.onError(error);
        });
    };
    CrudUserbaseListComponent.prototype.getRoles = function () {
        var _this = this;
        this.roleMappingService.getRoles().subscribe(function (data) {
            _this.crudRecord.profileList = data;
        }, function (error) {
            console.error("Error", error);
            _this.onError(error);
        });
    };
    // ERROR
    CrudUserbaseListComponent.prototype.onError = function (error) {
        this.alertService.error(error.status);
        this.crudRecord.alertMessage.alertTitle = "Error del servidor";
        this.crudRecord.alertMessage.alertText = error.statusText;
        this.crudRecord.alertMessage.alertShow = true;
        this.crudRecord.alertMessage.alertClass =
            "alert alert-danger alert-dismissible fade show";
    };
    // DETECT EVENTS ON DATATABLE
    CrudUserbaseListComponent.prototype.onActivate = function (event) { };
    CrudUserbaseListComponent.prototype.onSelect = function (event) {
        this.crudRecord.currentRecord = event.selected[0];
        this.crudRecord.buttonEditRecord.value = true;
    };
    CrudUserbaseListComponent.prototype.onExport = function () { };
